<template>
  <div class="setting-thirdParty">
    <div class="page-head">{{formData.setting.name}}</div>
    <el-form class="we7-panel-form" status-icon label-position="left"
      :model="formData"
      ref="thirdPartyForm"
      :rules="rules">
      <div class="we7-panel-form__header">
        {{formData.setting.name}}
        <el-button type="text" @click="editStatus = true"></el-button>
      </div>
      <div class="we7-panel-form__body">
        <el-form-item :label="formData.setting.name + ''" label-width="290px">
          <el-switch v-model="formData.setting.enable"
            :active-value="1"
            :inactive-value="0"
            :disabled="!editStatus">
          </el-switch>
        </el-form-item>
        <el-form-item label="" label-width="290px" prop="setting.name">
          <el-input v-model="formData.setting.name" v-if="editStatus"></el-input>
          <span v-else>{{formData.setting.name}}</span>
        </el-form-item>
        <el-form-item label="LOGO" label-width="290px" prop="setting.logo"
          v-if="!formData.is_default">
          <el-upload
            class="avatar-uploader"
            action="/admin/upload/image"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
             v-if="editStatus">
            <img v-if="formData.setting.logo" :src="formData.setting.logo" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <img :src="formData.setting.logo" class="avatar" v-else>
        </el-form-item>
        <el-form-item label="APPID" label-width="290px" prop="setting.app_id">
          <el-input v-model="formData.setting.app_id" v-if="editStatus"></el-input>
          <span v-else>{{formData.setting.app_id}}</span>
        </el-form-item>
        <el-form-item label="SERCETkey" label-width="290px" prop="setting.secret_key">
          <el-input v-model="formData.setting.secret_key" v-if="editStatus"></el-input>
          <span v-else>{{formData.setting.secret_key}}</span>
        </el-form-item>
        <el-form-item label="ACCESSTOKEN  API" label-width="290px" prop="setting.access_token_url">
          <el-input v-model="formData.setting.access_token_url" v-if="editStatus"></el-input>
          <span v-else>{{formData.setting.access_token_url}}</span>
        </el-form-item>
        <el-form-item label="  API" label-width="290px" prop="setting.user_info_url">
          <el-input v-model="formData.setting.user_info_url" v-if="editStatus"></el-input>
          <span v-else>{{formData.setting.user_info_url}}</span>
        </el-form-item>
      </div>
      <template v-if="!formData.is_default">
        <div class="we7-panel-form__header"></div>
        <div class="we7-panel-form__body">
          <el-form-item label="uid" label-width="290px">
            <el-input v-model="formData.convert.uid" v-if="editStatus"></el-input>
            <span v-else>{{formData.convert.uid}}</span>
          </el-form-item>
          <el-form-item label="username" label-width="290px">
            <el-input v-model="formData.convert.username" v-if="editStatus"></el-input>
            <span v-else>{{formData.convert.username}}</span>
          </el-form-item>
        </div>
      </template>
    </el-form>
    <el-button type="primary" @click="save"></el-button>
    <el-button @click="editStatus = false"></el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      editStatus: false,
      formData: {
        is_default: false,
        setting: {
          name: '',
          enable: '',
          logo: '',
          app_id: '',
          secret_key: '',
          access_token_url: '',
          user_info_url: ''
        },
        convert: {
          uid: '',
          username: ''
        }
      },
      rules: {
        'setting.name': [
          { required: true, message: '', trigger: 'blur' }
        ],
        'setting.logo': [
          { required: true, message: 'LOGO', trigger: 'blur' }
        ],
        'setting.app_id': [
          { required: true, message: 'APPID', trigger: 'blur' }
        ],
        'setting.secret_key': [
          { required: true, message: 'SERCETkey', trigger: 'blur' }
        ],
        'setting.access_token_url': [
          { required: true, message: 'ACCESSTOKEN', trigger: 'blur' }
        ],
        'setting.user_info_url': [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    $route: {
      handler: function(){
        this.init()
      },
      deep: true
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.$post('/admin/third-party-login/detail', {
        id: this.$route.query.id
      })
        .then(res => {
          this.formData.is_default = res.is_default
          this.formData.setting = {
            name: res.setting.name || '',
            enable: res.setting.enable || '',
            logo: res.setting.logo || '',
            app_id: res.setting.app_id || '',
            secret_key: res.setting.secret_key || '',
            access_token_url: res.setting.access_token_url || '',
            user_info_url: res.setting.user_info_url || ''
          }
          this.formData.convert = res.convert || {}
        })
    },
    handleAvatarSuccess(res) {
      this.formData.setting.logo = res.data.url
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(' JPG !');
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      return isJPG && isLt2M;
    },
    save() {
      this.$refs.thirdPartyForm.validate((valid) => {
        if (valid) {
          let data = {
            id: this.$route.query.id,
            setting: {
              ...this.formData.setting
            }
          }
          if (!this.formData.is_default) {
            data['convert'] = {
              ...this.formData.convert
            }
          }
          this.$post('/admin/third-party-login/update', data)
            .then(() => {
              this.$message('')
              this.editStatus = false
            })
        } else {
          this.editStatus = true
        }
      })
    }
  }
}
</script>

<style lang="scss">
.setting-thirdParty {
  .page-head {
    padding-bottom: 0;
  }
}
</style>