<template>
  <div class="setting-login">
    <div class="page-head"></div>
    <div class="we7-panel-form">
      <div class="we7-panel-form__header"></div>
      <div class="we7-panel-form__body">
        <div class="we7-panel-form__item">
          <div class="we7-panel-form__label"></div>
            <div class="we7-panel-form__value">
              <template v-if="loginMethod == 1">
                {{default_login_name}}
              </template>
              <template v-else>
                {{is_need_bind == '1' ? '' : ''}}
              </template>
            </div>
            <div class="we7-panel-form__action">
              <el-tooltip effect="dark" content="" placement="bottom">
                <i class="wi wi-edit" @click="dialogVisible = true"></i>
              </el-tooltip>
            </div>
        </div>
      </div>
    </div>
    <!--  -->
    <el-dialog class="we7-dialog" title="" :visible.sync="dialogVisible" :close-on-click-modal="false" center>
      <el-form label-width="120px" label-position="left">
        <el-form-item label="">
          <el-radio-group v-model="loginMethod" @change="change">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
          <div class="we7-help-block"></div>
        </el-form-item>
        <el-form-item label="" v-if="loginMethod == 0">
          <el-select v-model="is_need_bind" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" v-if="loginMethod == 1">
          <el-select v-model="default_login_channel" placeholder="">
            <el-option
              v-for="item in thirdParty"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <div class="we7-help-block" v-if="thirdParty.length"></div>
          <el-button class="textBtn" type="text" @click="goto" v-else></el-button>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmBtn"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      loginMethod: 0,
      is_need_bind: '0',
      default_login_channel: '',
      default_login_name: '',
      thirdParty: [],
      thirdPartyFisrtId: ''//idfalse
    }
  },
  created() {
    this.init()
    this.getThirdParty()
  },
  methods: {
    init() {
      this.$post('/admin/third-party-login/get-default-channel')
        .then(res => {
          if (res) {
            //default_login_channel  qqid
            //is_need_bind  true
            if (res.default_login_channel) {
              this.loginMethod = 1
              this.default_login_channel = res.default_login_channel
            } else {
              this.is_need_bind = res.is_need_bind ? '1' : '0'
            }
          }
        })
    },
    getThirdParty() {
      this.$post('/admin/third-party-login/all')
        .then(res => {
          this.thirdParty = []
          for (const i in res) {
            if (i == 0) {
              this.thirdPartyFisrtId = res[i].id
            }
            if (this.default_login_channel == res[i].id) {
              this.default_login_name = res[i].name
            }
            if (res[i].enable) {
              this.thirdParty.push({
                id: res[i].id + '',
                name: res[i].name
              })
              if (!this.default_login_channel) {
                this.default_login_channel = res[i].id + ''
              }
            }
          }
        })
    },
    change(val) {
      if (val == 1 && !this.thirdParty.length) {
        this.getThirdParty()
      }
    },
    goto() {
      let routeUrl = this.$router.resolve({
        path: "/admin/setting/thirdParty?id=" + this.thirdPartyFisrtId
      })
      window.open(routeUrl.href, '_blank')
    },
    confirmBtn() {
      let data = {}
      if (this.loginMethod == 0) {
        data = {is_need_bind: this.is_need_bind}
      } else {
        data = {default_login_channel: this.default_login_channel}
      }
      this.$post('/admin/third-party-login/set-default-channel', data)
        .then(res => {
          this.$message('')
          if (this.loginMethod == 1) {
            
          }
          this.dialogVisible = false
        })
    }
  }
}
</script>

<style lang="scss">
.setting-login {
  .page-head {
    padding-bottom: 0;
  }
  .textBtn {
    padding-left: 0;
  }
}
</style>