<template>
  <div class="admin-login">
    <div class="login-box">
      <h2></h2>
      <el-tabs v-model="active">
        <el-tab-pane label="" name="first">
          <div class="login-form">
            <el-input v-model="formData.username" prefix-icon="el-icon-user-solid" placeholder="/"></el-input>
            <el-input type="password" v-model="formData.userpass" prefix-icon="el-icon-s-goods" placeholder=""></el-input>
            <el-input class="code-input" v-model="formData.code" prefix-icon="el-icon-s-goods" placeholder="" @keyup.enter.native="login">
              <img :src="code" @click="getCode" slot="append" alt="">
            </el-input>
          </div>
          <!-- <div class="login-action">
            <el-button type="text" @click="showFind">?</el-button>
          </div> -->
          <div class="login-thirdParty" v-if="thirdPartyList.length">
            <span class="title"></span>
            <div class="icon-list">
              <img class="icon-block"
                v-for="icon in thirdPartyList" :key="icon.name" 
                :src="icon.logo"
                :title="icon.name"
                @click="thirdPartyIconClick(icon.redirect_url)">
            </div>
          </div>
          <el-button class="login-btn" @click="login"></el-button>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="footer">
      Powered by <a href="https://www.w7.cc">www.w7.cc</a>
    </div>
  </div>
</template>

<script>
import axios from '@/utils/axios'
import {replaceParamVal} from '@/utils/utils'
export default {
  name: 'adminLogin',
  data() {
    return {
      autofocus: false,
      active: 'first',
      code: '',
      formData: {
        username: '',
        userpass: '',
        code: ''
      },
      thirdPartyList: []
    }
  },
  beforeRouteEnter(to, from, next) {
    let code = to.query.code//code
    let redirect_url = to.query.redirect_url//url
    let app_id = to.query.app_id
    if (code) {
      axios.post('/common/auth/third-party-login', {
        code,
        app_id
      })
        .then(res => {
          if (res && res.is_need_bind) {//
            next('/bind')
          } else {
            if (!redirect_url) {
              next('/admin/document')
            } else {
              window.open(redirect_url, '_self')
            }
          }
        })
        .catch(() => {
          next('/login')
        })
    } else {
      axios.post('/common/auth/default-login-url')
        .then(res => {
          if (res) {
            window.open(res, '_self')
          } else {
            next()
          }
        })
    }
  },
  created () {
    this.getCode()
    this.getThirdParty()
  },
  methods: {
    showFind() {
      this.$message({message: ''})
    },
    getCode() {
      this.$post('/common/verifycode/image')
        .then(res => {
          if(res.img) {
            this.code = res.img
          }
        })
    },
    login() {
      for(let index in this.formData) {
        if(!this.formData[index]) {
          this.$message('')
          return false
        }
      }
      this.$post('/common/auth/login', this.formData)
        .then(() => {
          let msg = this.$message('')
          setTimeout(() => {
            msg.close()
            if (this.$route.query && this.$route.query.redirect_url) {
              window.open(this.$route.query.redirect_url, '_self')
            } else {
              this.$router.push('/admin/document')
            }
          }, 500)
        }).catch(() => {
          this.formData.code = ''
          document.getElementsByClassName("el-input__inner")[2].focus()
          this.getCode()
        })
    },
    getThirdParty() {
      this.$post('/common/auth/method', {
        redirect_url: this.$route.query.redirect_url
      })
        .then(res => {
          this.thirdPartyList = res || []
        })
    },
    thirdPartyIconClick(url) {
      window.open(url, '_self')
    }
  }
}
</script>

