<template>
  <el-container class="layout-container">
    <el-aside class="admin-view-aside" :width="isCollapse ? '64px' : '240px'">
      <el-menu class="admin-view-menu"
        :default-active="active"
        :router="true"
        :collapse="isCollapse">
        <el-menu-item index="/admin/setting">
          <i class="wi wi-cunchushebei"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-submenu index="1">
          <template slot="title">
            <i class="wi wi-system-login-settings"></i>
            <span></span>
          </template>
          <template v-for="(menu, index) in thirdPartyLoginMenu">
            <el-menu-item :index="'/admin/setting/thirdParty?id='+ menu.id" :key="index">{{menu.name}}</el-menu-item>
          </template>
          <el-menu-item index="/admin/setting/thirdPartyCustom"></el-menu-item>
        </el-submenu>
        <el-menu-item index="/admin/setting/login">
          <i class="wi wi-system-login-settings"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main>
      <router-view></router-view>
    </el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      active: '/admin/setting',
      isCollapse: false,
      thirdPartyLoginMenu: []
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.active = to.path
    })
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.$post('/admin/third-party-login/all')
        .then(res => {
          this.thirdPartyLoginMenu = res
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-main {
  padding: 0 25px 0 15px;
}
</style>