<template>
  <div class="admin-login">
    <div class="login-box">
      <h2></h2>
      <el-tabs v-model="active">
        <el-tab-pane label="" name="first">
          <div class="login-form">
            <el-input v-model="formData.username" prefix-icon="el-icon-user-solid" placeholder="/"></el-input>
            <el-input type="password" v-model="formData.userpass" prefix-icon="el-icon-s-goods" placeholder=""></el-input>
            <el-input class="code-input" v-model="formData.code" prefix-icon="el-icon-s-goods" placeholder="" @keyup.enter.native="login">
              <img :src="code" @click="getCode" slot="append" alt="">
            </el-input>
          </div>
          <!-- <div class="login-action">
            <el-button type="text" @click="showFind">?</el-button>
          </div> -->
          <el-button class="login-btn" @click="login"></el-button>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="footer">
      Powered by <a href="https://www.w7.cc">www.w7.cc</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'adminLogin',
  data() {
    return {
      autofocus: false,
      active: 'first',
      code: '',
      formData: {
        username: '',
        userpass: '',
        code: ''
      },
      thirdPartyList: []
    }
  },
  created () {
    this.getCode()
  },
  methods: {
    showFind() {
      this.$message({message: ''})
    },
    getCode() {
      this.$post('/common/verifycode/image')
        .then(res => {
          if(res.img) {
            this.code = res.img
          }
        })
    },
    login() {
      for(let index in this.formData) {
        if(!this.formData[index]) {
          this.$message('')
          return false
        }
      }
      this.$post('/common/auth/login', this.formData)
        .then(() => {
          let msg = this.$message('')
          setTimeout(() => {
            msg.close()
            this.$router.push('/admin/document')
          }, 500)
        }).catch(() => {
          this.formData.code = ''
          document.getElementsByClassName("el-input__inner")[2].focus()
          this.getCode()
        })
    }
  }
}
</script>